import { Breadcrumbs, Button, ButtonBase, FormControl, IconButton, Input, List, ListItem, ListItemIcon, ListItemSecondaryAction, ListItemText, Modal, Paper, Select, Switch, TextField, Typography } from "@material-ui/core";
import { Link, navigate } from "gatsby";
import * as React from "react";
import { useUserInfo } from "../../../components/save";
import Defer from "../../../components/ui/Defer";
import Navbar from "../../../components/ui/Navbar";
import Ambulan from '@material-ui/icons/LocalHospital';
import Kematian from '@material-ui/icons/AirlineSeatFlat';
import Kecelakaan from '@material-ui/icons/Commute';
import Kerusakan from '@material-ui/icons/DomainDisabled';
import Kejahatan from '@material-ui/icons/Warning';
import Bencana from '@material-ui/icons/Pool';
import AddIcon from '@material-ui/icons/Add';
import EditIcon from '@material-ui/icons/Edit';
import DelIcon from '@material-ui/icons/Delete';
import { useModalTheme } from "../../../components/style";
import ajax, { useFetcher } from "../../../components/ajax";
import { winconfirm } from "../../../components/util";

const icons = {
    ambulan: Ambulan,
    kematian: Kematian,
    kecelakaan: Kecelakaan,
    kerusakan: Kerusakan,
    kejahatan: Kejahatan,
    bencana: Bencana,
}

const Prompt = ({ inquiry, open, onClose, onAction }) => {
    const modalTheme = useModalTheme();
    const [name, setName] = React.useState(inquiry?.name);
    const [whatsapp, setWhatsapp] = React.useState(inquiry?.whatsapp);
    const [number, setNumber] = React.useState(inquiry?.number);
    const [service, setService] = React.useState(inquiry?.service);
    return (
        <Modal
            open={open}
            onClose={onClose}
        >
            {open && <Paper className={modalTheme.paper} elevation={5}><List>
                <ListItem>
                    <ListItemText primary="Nama Kontak" />
                    <ListItemSecondaryAction>
                        <FormControl style={{ minWidth: '40vw' }}>
                            <TextField value={name} onChange={(e) => setName(e.target.value)} />
                        </FormControl>
                    </ListItemSecondaryAction>
                </ListItem>
                <ListItem>
                    <ListItemText primary="Nomor Kontak" />
                    <ListItemSecondaryAction>
                        <FormControl style={{ minWidth: '40vw' }}>
                            <TextField autoComplete="tel" value={number} onChange={(e) => setNumber(e.target.value)} />
                        </FormControl>
                    </ListItemSecondaryAction>
                </ListItem>
                <ListItem>
                    <ListItemText primary="Dapat dikontak dengan Whatsapp?" />
                    <ListItemSecondaryAction>
                        <Switch checked={whatsapp} onChange={(e) => setWhatsapp(e.target.checked)} />
                    </ListItemSecondaryAction>
                </ListItem>
                <ListItem>
                    <ListItemText primary="Jenis Layanan" />
                    <ListItemSecondaryAction>
                        <FormControl style={{ minWidth: '40vw' }}>
                            <Select native value={service} onChange={(e) => setService(e.target.value)}>
                                {Object.keys(icons).map(x => <option key={x}>{x}</option>)}
                            </Select>
                        </FormControl>
                    </ListItemSecondaryAction>
                </ListItem>
                <ListItem>
                    <Button onClick={onClose} variant="outlined">Batal</Button>
                    <Button onClick={() => name && number && onAction({
                        ...inquiry,
                        name,
                        whatsapp,
                        number,
                        service
                    })} className={modalTheme.right} variant="contained">Simpan</Button>
                </ListItem>
            </List></Paper>}
        </Modal>
    );
}

const Page = () => {
    const user = useUserInfo();
    const [rel, setRel] = React.useState(1);
    const contactList = useFetcher(`/desa/emergency/?refresh=${rel}`);
    const [editData, setEditData] = React.useState(null);
    const save = (data) => {
        if (data.idx === -1) {
            contactList.data.push(data);
        } else {
            contactList.data[data.idx] = data;
        }
        delete data.idx;
        ajax.post('/desa/emergency/', contactList.data).then(() => {
            alert('Berhasil disimpan');
        }).catch(x => {
            alert(x.response?.data.message || x.message);
        }).finally(x => {
            setRel(rel + 1);
            setEditData(null);
        });
    }

    const del = (idx) => {
        winconfirm(`Yakin untuk menghapus kontak ini?`).then(r => {
            if (r) {
                contactList.data.splice(idx, 1);
                ajax.post('/desa/emergency/', contactList.data).then(() => {
                    alert('Berhasil disimpan');
                }).catch(x => {
                    alert(x.response?.data.message || x.message);
                }).finally(x => {
                    setRel(rel + 1);
                    setEditData(null);
                });
            }
        });
    }
    return (
        <div>
            <Navbar />
            <List>
                <ListItem>
                    <Breadcrumbs>
                        <Link color="inherit" to="/desa/">
                            Admin Desa  {user && user.geolookup.kelurahan}
                        </Link>
                        <Typography color="textPrimary">Layanan Darurat</Typography>
                    </Breadcrumbs>
                    <ListItemSecondaryAction>
                        <Button startIcon={<AddIcon />} variant="contained" color="primary" onClick={() => setEditData({
                            idx: -1,
                            number: '',
                            whatsapp: false,
                            name: '',
                            service: 'ambulan',
                        })}>
                            Tambah Kontak
                        </Button>
                    </ListItemSecondaryAction>
                </ListItem>
                <Defer data={contactList}>
                    {() => contactList.data.map((x, i) =>
                        <ListItem key={i} button onClick={() => setEditData({ idx: i, ...x })}>
                            <ListItemIcon style={{justifyContent: 'center'}}>
                                {icons[x.service] && React.createElement(icons[x.service])}
                            </ListItemIcon>
                            <ListItemText style={{ textAlign: 'left', marginLeft: 10 }} primary={x.name} secondary={`${x.number} untuk layanan ${x.service}`} />
                            <ListItemSecondaryAction>
                                <IconButton onClick={() => setEditData({ idx: i, ...x })}>
                                    <EditIcon />
                                </IconButton>
                                <IconButton onClick={() => del(i)}>
                                    <DelIcon />
                                </IconButton>
                            </ListItemSecondaryAction>
                        </ListItem>
                    )}
                </Defer>
            </List>
            <Prompt key={editData ? editData.idx : -2} inquiry={editData} open={editData !== null} onClose={() => setEditData(null)} onAction={save} />
        </div>
    );
}

export default Page
